<template lang="pug">
#project-modal
  .modal-content
    .close(@click="closeModal")
      svg.bi.bi-x(
        width="32px",
        height="32px",
        viewBox="0 0 16 16",
        fill="#2B4B8A",
        xmlns="http://www.w3.org/2000/svg"
      )
        path(
          fill-rule="evenodd",
          d="M4.646 4.646a.5.5 0 0 1 .708 0L8 7.293l2.646-2.647a.5.5 0 0 1 .708.708L8.707 8l2.647 2.646a.5.5 0 0 1-.708.708L8 8.707l-2.646 2.647a.5.5 0 0 1-.708-.708L7.293 8 4.646 5.354a.5.5 0 0 1 0-.708z"
        )
    .gif
      img(:src="selectedProject.gif") 
    .project-name 
      p {{ selectedProject.projectName }}
    .about
      p.label About
      p {{ selectedProject.description }}
    .motivation(v-if="selectedProject.motivation")
      p.label Motivation
      p
        | {{ selectedProject.motivation }}
    .responsibilities(v-if="selectedProject.responsibilities")
      p.label Responsibilities
      p(
        v-for="(resp, index) in selectedProject.responsibilities",
        :key="'resp-' + index"
      ) 
        | {{ resp }}
    .links
      p.label Links to the site:
      p(v-if="selectedProject.links.length <= 0") Unfortunately, this site is not accessible by the public :(
      .link(
        v-for="(link, index) in selectedProject.links",
        :key="'link-' + index"
      )
        a(:href="link", target="_blank")
          | {{ link }}
</template>

<script>
export default {
  computed: {
    selectedProject() {
      return this.$store.state.selectedProject;
    },
  },
  methods: {
    closeModal() {
      this.$store.commit("setShowProjectModal", false);
    },
  },
};
</script>

<style lang="sass" scoped>
#project-modal
  position: fixed
  width: 100vw
  height: 100vh
  z-index: 1000
  background-color: rgba(0, 0, 0, 0.5)
  top: 0%
  left: 0%

.modal-content
  height: 80%
  max-width: 1024px
  position: absolute
  background-color: #2b7b8a
  top: 50%
  left: 50%
  transform: translate(-50%, -50%)
  overflow: auto
  p
    color: #c7e4e9
  .close
    position: fixed
    right: 20px
    top: 20px
    cursor: pointer
  .gif
    margin-top: 20px
    width: 100%
    height: 50%
    img
      height: 100%
  .project-name
    text-align: center
    p
      color: #87ceeb
  .about
    padding: 0px 20px
    text-align: left
  .motivation
    margin-top: 20px
    padding: 0px 20px
    text-align: left
  .responsibilities
    margin-top: 20px
    padding: 0px 20px
    text-align: left
  .links
    margin-top: 20px
    padding: 0px 20px 20px
    text-align: left
    .link
      a
        color: #c7e4e9
        cursor: pointer
.label
  font-weight: bold
  padding-bottom: 10px
  color: #42b983 !important
</style>
